<template>
  <v-container fluid>
    <v-row no-gutters>
      <v-col cols="6">
        <h1 class="black--text font-weight-light">测试程序配置</h1>
      </v-col>
      <v-col cols="4">
        <v-select
          v-model="package1"
          :items="cases"
          label="Choose Package"
        ></v-select>
      </v-col>
      <v-col cols="2">
        <v-btn color="primary" @click="setConfigs">Save</v-btn>
      </v-col>

      <v-col cols="12" v-for="(config, section, index) in configs" :key="index">
        <v-hover v-slot="{ hover }">
          <v-card
            :elevation="hover ? 5 : 3"
            :class="{ 'on-hover': hover }"
          >
            <v-card-title class="text-h5  ">
              {{ section}}
            </v-card-title>
            <v-card-text>
              <v-row no-gutters>
                <v-col cols="3" v-for="(value, key, index1) in config" :key="index1">
                  <v-text-field
                    dense
                    :label="key"
                    v-model="configs1[section][key]"
                    outlined
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-hover>
        <v-divider></v-divider>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { KunlunApi } from '../api'

export default {
  name: 'ConfigPage',
  components: {
  },
  data: () => ({
    url: '',
    timeOutCount: null,
    cases: [],
    configs: {},
    configs1: {},
    modified: '',
    package1: ''
  }),
  mounted () {
    this.getConfigCases()
  },
  beforeDestroy () {
  },
  watch: {
    package1 () {
      this.getConfigs()
    }
  },
  methods: {
    getConfigs () {
      KunlunApi({ service: 'get-configs', case: this.package1 })
        .then(response => {
          this.configs = response.data.data.configs
          this.configs1 = response.data.data.configs
          this.modified = response.data.data.modified
        })
        .catch(e => {
          console.log(e)
        })
    },
    setConfigs () {
      KunlunApi({ service: 'set-configs', case: this.package1, configs: this.configs1, modified: this.modified })
        .then(response => {
          let message = response.data.message
          console.log(message)
          this.getConfigs()
        })
        .catch(e => {
          console.log(e)
        })
    },
    getConfigCases () {
      KunlunApi({ service: 'get-config-cases' })
        .then(response => {
          this.cases = response.data.data
          this.package1 = this.cases[0]
        })
        .catch(e => {
          console.log(e)
        })
    }
  }
}
</script>
<style scoped>

</style>